.cta-button {
  background: #ffffff;
  border: 1px solid #CFADAC;
  border-radius: 6px;
  color: #967675;
  display: inline-block;
  font-size: 16px;
  line-height: 24px;
  padding: 18px 47px 19px 47px;
  position: relative;
  text-decoration: none;
  transition: all 0.5s;
}
@media (max-width: 500px) {
  .cta-button {
    display: block;
  }
}
.cta-button::after {
  position: absolute;
  width: 87%;
  height: 32px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  box-shadow: 0px 8px 34px rgba(150, 79, 76, 0.32);
  border-radius: 6px;
  content: "";
  display: block;
  z-index: -1;
}

